import React, { Component } from "react";
import styleSections from './Sections.css'
import {ReactComponent as ArrowDown} from "../Svgs/1.svg";


class Section1 extends Component {
    render() {
        return(
            <section className="first">
                <div className="first__header">
                    <nav className="first__nav">
                        <ul className="first__ul">
                            <a href="#section2"><li className="first__list">Web</li></a>
                            <a href="#section3"><li className="first__list">3D</li></a>
                            <a href="#section4"><li className="first__list">Contact</li></a>
                        </ul>
                    </nav>
                </div>
                <div className="first__content">
                    <h1 className="first__title">Mario'<span className="first__s">s</span> <br /> <span className="first__portfolio"><p>Portfolio</p> </span></h1>
                </div>
                <a href="#section2">
                    <div className="first__arrow">
                        <ArrowDown fill="white"/>
                        
                    </div>
                </a>
            </section>
        );
    }
}

export default Section1;

