import React, { Component } from "react";
import styleSections from './Sections4.css'

class Section4 extends Component {
    
    render() {
        const buttonName = "Submit";
        return (
            <div className="four" id="section4">
                <div className="four__bg"></div>
                <div className="four__content">
                    <h1 className="four__title">Contact me!</h1>
                    <form action="" className="four__form">
                        <div className="four__table">
                            <label htmlFor="fname">Name</label>
                            <input type="text" id="fname" placeholder="Jonny" />
                            <label htmlFor="sname">Surname</label>
                            <input type="text" id="sname" placeholder="Smith" />
                            <label htmlFor="email">E-mail</label>
                            <input type="text" id="email" placeholder="something@mail.com" />
                        </div>
                        <input className="button" type="submit" value={buttonName} />
                    </form>
                </div>
            </div>
        );
    }
}

export default Section4;