import React, { useState } from "react";
import styleSections from './Sections2.css'

import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";
import { useRef, useEffect } from "react";
gsap.registerPlugin(ScrollTrigger);



export default function Section2() {
    const [list, setList] = useState({
        bg: "",
        bgm: ""
    });

    const handleClick1 = () => {
        setList({
            bg: "test",
            bgm: "test-m"
        })
    }
    const handleClick2 = () => {
        setList({
            bg: "test2",
            bgm: "test2-m"
        })
    }
    const handleClick3 = () => {
        setList({
            bg: "test3",
            bgm: "test3-m"
        })
    }


    // Trying to apply the animation

     // inialising leftBox
     const leftBox = useRef(null);
     useEffect(()=> {
         // Here we use GSAP
         // To use it we need an element to reference in jsx
         const el = leftBox.current;
         //First object is FROM, second is TO
         gsap.fromTo(
             //Object
             el, 
             //From
             {
                 // rotation:0,
                 x:"-280%", y:"0%",
                 opacity: 0,
                 scale: .6
             },
             //To
             {
                  // rotation: -180,
                  x:"0%", y:"0%",
                  opacity: 1,
                  duration: 2,
                  scrollTrigger: {
                      trigger: el 
                   },
                   scale: 1
                   
                  //  delay: 1 
             })
 
     },[])
     const rightBox = useRef(null);
     useEffect(()=> {
         // Here we use GSAP
         // To use it we need an element to reference in jsx
         const el1 = rightBox.current;
         //First object is FROM, second is TO
         gsap.fromTo(el1, 
             {
                 // rotation:0,
                 x:"280%", y:"0%",
                 opacity: 0,
                 scale: .6
                 
             },
             {
                 // rotation: -180,
                 x:"0%", y:"0%",
                 opacity: 1,
                 duration: 2,
                 scrollTrigger: {
                     trigger: el1 
                  },
                  scale: 1
                 //  delay: 1 
                 
             })
 
     },[])
     const sectionTwo = useRef(null);

     const ctrlRef = useRef(null);
     useEffect(()=> {
         // Here we use GSAP
         // To use it we need an element to reference in jsx
         const el3 = ctrlRef.current;
         const section2 = sectionTwo.current;
         //First object is FROM, second is TO
         gsap.fromTo(el3, 
             {
                 // rotation:0,
                 x:"0%", y:"20%",
                 opacity: 0,
                 scale: .6
                 
             },
             {
                 // rotation: -180,
                 x:"0%", y:"0%",
                 opacity: 1,
                 duration: 3,
                 scrollTrigger: {
                     trigger: section2 
                  },
                  scale: 1
                 //  delay: 1 
                 
             })
 
     },[])
    // End of animation

    return(
        <div ref={sectionTwo} className={`second`} id="section2">
           
            <h1 className="second__title">Web</h1>
            <div className="second__content">

                <div className="second__top">
                    <div ref={leftBox} className="second__desktop">
                        <h2 className="second__subtitle second__subtitle--desktop">Desktop</h2>
                        <div  className={`${list.bg} second__image second__image--desktop`}></div>
                    </div>
                    <div ref={rightBox} className="second__mobile">
                        <h2  className="second__subtitle second__subtitle--mobile">Mobile</h2>
                        <div  className={`${list.bgm} second__image second__image--mobile`}></div>
                    </div>
                </div>

                <div ref={ctrlRef} className="second__controls">
                    <button onMouseOver={handleClick1} className="second__button second__button--first">1</button>
                    <button onMouseOver={handleClick2} className="second__button second__button--second">2</button>
                    <button onMouseOver={handleClick3} className="second__button second__button--third">3</button>

                    
                </div>
                
                {/* End of content */}
            </div>
        </div>
    );
};